
import { onBeforeUnmount, ref } from "vue"
import { UUID } from "./lib/uuid"
import twPopover from "./tw-popover.vue"

const log = require("debug")("ui:tooltip")

export default {
  components: {
    twPopover,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      target: null,
      id: UUID(), // https://accessibility.athena-ict.com/aria/examples/tooltip.shtml
      hoverCount: 0,
    }
  },
  // computed: {
  //   slottedTitle() {
  //     return this.title || this.$slots?.content?.[0] != null
  //   }
  // },

  setup(props: any) {
    let tooltipAnchor = ref()
    let target = ref<Node>()
    let hoverCount = 0

    const methods = {
      showTooltip() {
        target.value = tooltipAnchor.value
      },
      hideTooltip() {
        // @ts-ignore
        target.value = null
        hoverCount = 0
      },
      onMouseEnter() {
        hoverCount += 1
        if (hoverCount === 1) {
          methods.showTooltip()
        }
      },
      onMouseLeave() {
        hoverCount -= 1
        if (hoverCount <= 0) {
          methods.hideTooltip()
        }
      },
    }

    onBeforeUnmount(methods.hideTooltip)

    return {
      ...methods,
      target,
      tooltipAnchor,
    }
  },
}
